export const clearStyle = {
  methods: {
    initClearStyle () {
      let clearStyleBtn = this.createBtn('<i title="" class="w-icon-filter"></i>')
      this.toolbar.appendChild(clearStyleBtn)
      clearStyleBtn.addEventListener('click', _ => {
        let html = this.clearStyle()
        this.editor.cmd.do('insertHTML', html)
        this.editor.txt.html(html)
      }, false)
    },
    clearStyle () {
      // editor.txt.html()<style>
      // <style>
      // editor.txt.html()HTML
      let arr = this.textWrap.getElementsByTagName('*')
      Array.from(arr).map(item => {
        if (item.style) {
          item.removeAttribute('style')
        }
        if (item.className) {
          item.removeAttribute('class')
        }
      })
      // styleremoveAttribute
      return this.editor.txt.html()// textWrap.innerHTML
             .replace(/ style=\"(.*?)\"/ig, '')
             .replace(/ style=\'(.*?)\'/ig, '')
             .replace(/ class=\"(.*?)\"/ig, '')
             .replace(/ class=\'(.*?)\'/ig, '')
             // word
             .replace(/ lang=\"(.*?)\"/ig, '')
             .replace(/ face=\"(.*?)\"/ig, '')
             .replace(/ size=\"(.*?)\"/ig, '')
             .replace(/ align=\"(.*?)\"/ig, '')
             .replace(/ color=\"(.*?)\"/ig, '')
             .replace(/ border=\"(.*?)\"/ig, '')
             .replace(/<style>[\s\S]*?<\/style>/ig, '')
             .replace(/<o:p>[\s\S]*?<\/o:p>/ig, '')
             .replace(/<!--[\s\S]*?-->/ig, '')
    }
  }
}



